.carousel-caption {
  text-align: left !important;
  width: 50ch;
  left: 5% !important;
}

.carousel-caption p,
.carousel-caption h3 {
  text-shadow: 1px 2px 1px rgba(0, 0, 0, 1);
}

.card {
  border: none;
  margin: 0.2rem !important;
  display: flex !important;
  justify-content: center !important;
}

.card__content {
  display: flex;
  justify-content: center;
}

.card h5 {
  position: absolute;
  font-size: clamp(0.8rem, 2.5vw, 1.5rem);
  padding: 2px;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 20ch;
  opacity: 0;
  transform: translateY(-10px);
  transition: all 300ms ease-out;
  text-shadow: 2px 3px 2px rgba(0, 0, 0, 1);
}

.card .btn {
  align-self: flex-end;
  visibility: hidden;
  opacity: 0;
  transform: translateY(10px);
  transition: all 300ms ease-out;
  font-weight: bold;
}
.card:hover h5,
.card:hover .btn {
  opacity: 1;
  visibility: visible;
  transition: all 300ms ease-out;
  transform: translateY(0);
}

.react-stars {
  margin: auto !important;
}

.add {
  visibility: visible !important;
}

.btn-outline-info {
  border-radius: 40px !important;
  margin-bottom: 10px;
  border-radius: 25px;
  font-weight: 500;
}

.btn-outline-primary {
  border: 2px solid #007bff !important;
  font-weight: bolder !important;
}

.list-inline {
  text-align: center;
  transition: all 0.3s ease-in-out;
}

.list-inline .btn-outline-info:hover {
  background: transparent !important;
  transition: all 0.3s ease-in-out;
  box-shadow: 1px 1px 3px 1px rgba(200, 200, 200, 0.4);
  color: cadetblue;
}

.title {
  font-size: clamp(1rem, 2.5vw, 2rem);
  width: 20ch;
}
.container {
  scroll-behavior: smooth;
  text-align: center;
}
h3 {
  margin-top: 2rem;
  color: ghostwhite;
}

@media only screen and (max-width: 400px) {
  .card h5 {
    display: none;
  }
}
@media only screen and (max-width: 768px) {
  .carousel-caption {
    margin-bottom: -20px;
  }

  .carousel-caption .overview {
    display: none;
  }
}
